body{
    margin: 0;
}

hr {
    border-top: 2px solid rgb(255, 255, 255);
}

.navbar{
    /***** stay on top *****/
    position: -webkit-sticky;
    position: sticky;
    top: 0;

    display: flex;
    padding: 0px 0px;
    margin: 0;
    /*background: rgb(118, 112, 206);*/
    background-image:  linear-gradient(to right, rgba(0, 0, 255, 1), rgba(0, 0, 255, 0.5));

}

.navitem{
    margin: 0 auto;
    color: rgb(203, 196, 226);
    font-size: 20px;
    text-shadow: 1px -1px rgb(226, 214, 214);
}
.navitem:hover{
    transform: scale(1.1);
    /*color: rgb(103, 105, 182);*/
    text-decoration-line: underline;
    cursor: pointer;
}

.logoicon{
    height: 40px;
    width: 40px;
    padding: 5px 12px;
}

.logolink{
    height: 40px;
    width: 40px;
    padding: 5px 12px;
    color: rgb(0, 0, 200);
    filter: drop-shadow(0px 1px grey);
}

.logolink:hover{
    transform: scale(1.3);
    /*color: rgb(0, 0, 200, 0.5);*/
    filter: drop-shadow(0px 3px grey);
}


.home{
    text-align: center;
    padding: 230px 40px;
    margin: 0;
    background: #eee;
    /*height: 40vh;*/
}

.home h3{
    font-size: 44px;
    text-shadow: 2px 2px #aaa;
}
.home h4{
    font-size: 26px;
    text-shadow: 2px 2px #aaa;
}

.projects{
    text-align: center;
    padding: 40px;
    background: #eee;
}

.about{
    text-align: center;
    padding: 0px 40px 500px 40px;
    margin: 0;
    background: #eee;
    padding: 150px 0;
}

.contact{
    text-align: center;
    padding: 40px;
    margin: 0;
    background: #eee;
}

.proj-img{
    height:20em;
    width: 20em;
    padding: 0;
    margin: 0;
}